import React, { Component } from 'react'

export class Group extends Component {

    render() {

        const {title, points} = this.props.group;
        const id = this.props.id

        return (
            <div className="card text-center cardStyle">
                <div className="card-header">
                    <h4 className="card-title titleStyle">{ title }</h4>
                </div>
                <div className="card-body">
                    <p className="card-text">Aquest grup t√©:</p>
                    <h5> <b> { points } punts</b> </h5>

                </div>
                <div className="card-footer text-muted container">
                    <div className="row" style={{width: "100%", alignContent: "center"}}>
                        <div className="col-6" style={{width: "100%", alignItems: "center"}}>
                            <button type="button" className="btn btn-outline-danger" style={{width: "100%"}} onClick={this.props.restar.bind(this, id, 1)}>Restar 1 punt</button>
                        </div>
                        <div className="col-6">
                            <button type="button" className="btn btn-success" style={{width: "100%"}} onClick={this.props.sumar.bind(this, id, 1)}>Sumar 1 punt</button>
                        </div>
                    </div>
                    <br />
                    <div className="row" style={{width: "100%"}}>
                        <div className="col-6">
                            <button type="button" className="btn btn-outline-danger" style={{width: "100%"}} onClick={this.props.restar.bind(this, id, 2)}>Restar 2 punts</button>
                        </div>
                        <div className="col-6">
                            <button type="button" className="btn btn-success" style={{width: "100%"}} onClick={this.props.sumar.bind(this, id, 2)}>Sumar 2 punts</button>
                        </div>
                    </div>
                    <br />
                    <div className="row" style={{width: "100%"}}>
                        <div className="col-6">
                            <button type="button" className="btn btn-outline-danger" style={{width: "100%"}} onClick={this.props.restar.bind(this, id, 5)}>Restar 5 punts</button>
                        </div>
                        <div className="col-6">
                            <button type="button" className="btn btn-success" style={{width: "100%"}} onClick={this.props.sumar.bind(this, id, 5)}>Sumar 5 punts</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Group
