import React, { Component } from 'react'
import Group from './Group'

export class Groups extends Component {

    render() {

        if(this.props.render) {
            return this.props.groups.map((group) => (
                <Group group={group} key={this.props.groups.indexOf(group)} id={this.props.groups.indexOf(group)} sumar={this.props.sumar} restar={this.props.restar}/>
            ));
        } else {
            return(
                <div className="chargeStyle" ><h2>Carregant...</h2></div>
            )
        }
    }
}

export default Groups
