import React, { Component } from 'react'

import Header from './content/Header'
import Groups from './content/Groups'

import './App.css'

//Firebase
import firebase from 'firebase/app';
import 'firebase/database'

export class App extends Component {

  state = {
    render: false
  }

  componentWillMount() {
    var config = 
    {
      apiKey: "AIzaSyBuBiSWzJ5W9anISroytUCp40Y66Q9rjO8",
      authDomain: "grups-esplai.firebaseapp.com",
      databaseURL: "https://grups-esplai.firebaseio.com",
      projectId: "grups-esplai",
      storageBucket: "",
      messagingSenderId: "610242821482",
      appId: "1:610242821482:web:ac0cd7bf812fe610"
    };
  
    //Initializing Firebase
    firebase.initializeApp(config);
  
    // Reading the data from Firebase at start
    firebase.database().ref().on('value', data => {
      this.setState({
        groups: data.val(),
        render: true
      })
      console.log('Got data from Firebase');
    })
  }

  sumar = (id, value) => {
    var updates = {}
    updates[id + '/points/'] = this.state.groups[id].points + value
    firebase.database().ref().update(updates)

    console.log(`Sumats ${value} punts a ${id}`)
  }

  restar = (id, value) => {
    var updates = {}
    updates[id + '/points/'] = this.state.groups[id].points - value
    firebase.database().ref().update(updates)

    console.log(`Restats ${value} punts a ${id}`)
  }

  
  render() {

    // Rendering App.js
    return (
      <div className="appStyle">
        <Header />

        <center>
          <Groups groups={this.state.groups} render={this.state.render} sumar={this.sumar} restar={this.restar}/>
        </center>
      </div>
    )
  }
}

export default App
